import React, { useState, useEffect, useContext } from "react";
import {
  Drawer,
  Box,
  Typography,
  TextField,
  Button,
  Grid,
  Divider,
  Autocomplete,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  CircularProgress,
} from "@mui/material";
import { useMediaQuery } from "@mui/material";
import CloseIcon from "@mui/icons-material/Close";
import SaveIcon from "@mui/icons-material/Save";
import AddIcon from "@mui/icons-material/Add";
import DeleteIcon from "@mui/icons-material/Delete";
import VisibilityIcon from "@mui/icons-material/Visibility";
import {
  studentType,
  religions,
  financeType,
  races,
  maritalStatus,
  employmentStatus,
  highestEducation,
  nationality,
  provinces,
  yes_no,
} from "../../components/lov";
import { ThemeContext } from '../../config/ThemeContext';
import { DatePicker } from "@mui/x-date-pickers/DatePicker";
import { LocalizationProvider } from "@mui/x-date-pickers/LocalizationProvider";
import { AdapterDateFns } from "@mui/x-date-pickers/AdapterDateFns";
import { format, parse } from 'date-fns';

const API_BASE_URL = process.env.REACT_APP_API_BASE_URL;
const StudentDetailDrawer = ({ open, onClose, studentId, onSave, onDelete }) => {
  const { isDarkMode } = useContext(ThemeContext);
  const [selectedDate, setSelectedDate] = useState(null);
  const [emailError, setEmailError] = useState("");
  const [whatsappError, setWhatsappError] = useState("");
  const [alternativeError, setAlternativeError] = useState("");
  const [emergencyError, setEmergencyError] = useState("");
  const [emergencyContactOption, setEmergencyContactOption] = useState("");
  const [deleteConfirmationOpen, setDeleteConfirmationOpen] = useState(false);
  const [deleteAttachmentConfirmationOpen, setDeleteAttachmentConfirmationOpen] = useState(false);
  // const [successMessage, setSuccessMessage] = useState("");
  const [errorMessage, setErrorMessage] = useState(""); // Added for error feedback
  const [isLoading, setIsLoading] = useState(false); // Added for loading state
  const isLargeScreen = useMediaQuery("(min-width:600px)");
  const drawerWidth = isLargeScreen ? 500 : 330;

  const user = JSON.parse(localStorage.getItem("user"));
  const userType = user?.user_type; // student or admin
  const isAdmin = userType === "admin";

  const relationshipTypes = [
    "Staff",
    "Dependent of Staff",
    "Family",
    "Referral",
    "Director/Board Member or stakeholder"
  ];

  const [formData, setFormData] = useState({
    student_name: "",
    student_surname: "",
    student_nationality: "",
    student_id_passport_number: "",
    student_type: "",
    student_religion: "",
    student_finance_type: "",
    student_whatsapp_number: "",
    student_alternative_number: "",
    student_email_address: "",
    student_highest_education: "",
    student_home_address: "",
    student_suburb: "",
    student_area_code: "",
    student_province: "",
    student_date_of_birth: "",
    student_race: "",
    student_marital_status: "",
    student_employment_status: "",
    student_job_title: "",
    student_company_of_employment: "",
    student_current_salary: "",
    student_number_of_siblings: "",
    student_siblings_bursary: "",
    student_willow_relationship: "",
    relation_type: "",
    relation_hr_contact: "",
    relation_branch: "",
    relation_name: "",
    relation_surname: "",
    relation_employee_code: "",
    relation_reference: "",
    student_emergency_contact_name: "",
    student_emergency_contact_number: "",
    student_emergency_contact_relationship: "",
    student_emergency_contact_address: "",
    student_status: "Pending",
    student_status_comment: "",
    employment_status_attachment: null,
    employment_status_attachment_name: "",
  });

  const statusOptions = ["Received", "Pending", "Approved", "Declined"];

  const validateEmail = (email) => {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return emailRegex.test(email);
  };

  const validateNumber = (number) => {
    const numberRegex = /^\d{10}$/;
    return numberRegex.test(number);
  };

  useEffect(() => {
    if (open) {
      if (studentId) {
        const fetchStudentData = async () => {
          setIsLoading(true);
          try {
            const response = await fetch(`${API_BASE_URL}/student-details/${studentId}`);
            if (!response.ok) {
              throw new Error(`Failed to fetch student data: ${response.statusText}`);
            }
            const data = await response.json();
            const dates = data.student_date_of_birth ? parse(data.student_date_of_birth, 'MM/dd/yyyy', new Date()) : null;
            setSelectedDate(dates);
            setFormData((prev) => ({
              ...prev,
              ...data,
              student_date_of_birth: dates,
              student_status: data.student_status || "Pending",
              student_status_comment: data.student_status_comment || "",
              relation_type: data.relation_type || "",
              relation_hr_contact: data.relation_hr_contact || "",
              relation_branch: data.relation_branch || "",
              relation_name: data.relation_name || "",
              relation_surname: data.relation_surname || "",
              relation_employee_code: data.relation_employee_code || "",
              relation_reference: data.relation_reference || "",
              employment_status_attachment: null, // Don't preload binary data
              employment_status_attachment_name: data.employment_status_attachment_name || "",
            }));
            setEmergencyContactOption(data.student_emergency_contact_name ? "Add new" : "");
            setEmailError(data.student_email_address && !validateEmail(data.student_email_address) ? "Please enter a valid email address" : "");
            setWhatsappError(data.student_whatsapp_number && !validateNumber(data.student_whatsapp_number) ? "WhatsApp number must be exactly 10 digits" : "");
            setAlternativeError(data.student_alternative_number && !validateNumber(data.student_alternative_number) ? "Alternative number must be exactly 10 digits" : "");
            setEmergencyError(data.student_emergency_contact_number && !validateNumber(data.student_emergency_contact_number) ? "Emergency contact number must be exactly 10 digits" : "");
          } catch (error) {
            console.error("Error fetching student data:", error);
            setErrorMessage("Failed to load student data. Please try again.");
          } finally {
            setIsLoading(false);
          }
        };
        fetchStudentData();
      } else {
        setSelectedDate(null);
        setFormData({
          student_name: '',
          student_surname: '',
          student_nationality: '',
          student_id_passport_number: '',
          student_type: '',
          student_religion: '',
          student_finance_type: '',
          student_whatsapp_number: '',
          student_alternative_number: '',
          student_email_address: '',
          student_highest_education: '',
          student_home_address: '',
          student_suburb: '',
          student_area_code: '',
          student_province: '',
          student_date_of_birth: '',
          student_race: '',
          student_marital_status: '',
          student_employment_status: '',
          student_job_title: '',
          student_company_of_employment: '',
          student_current_salary: '',
          student_number_of_siblings: '',
          student_siblings_bursary: '',
          student_willow_relationship: '',
          relation_type: '',
          relation_hr_contact: '',
          relation_branch: '',
          relation_name: '',
          relation_surname: '',
          relation_employee_code: '',
          relation_reference: '',
          student_emergency_contact_name: '',
          student_emergency_contact_number: '',
          student_emergency_contact_relationship: '',
          student_emergency_contact_address: '',
          student_status: 'Pending',
          student_status_comment: '',
          employment_status_attachment: null,
          employment_status_attachment_name: '',
        });
        setEmergencyContactOption("");
        setEmailError("");
        setWhatsappError("");
        setAlternativeError("");
        setEmergencyError("");
        setErrorMessage("");
      }
    }
  }, [open, studentId]);

  const handleDateChange = (newDate) => {
    setSelectedDate(newDate);
    const formattedDate = newDate ? format(newDate, 'MM/dd/yyyy') : '';
    setFormData(prev => ({ ...prev, student_date_of_birth: formattedDate }));
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    let sanitizedValue = value;

    if (["student_whatsapp_number", "student_alternative_number", "student_emergency_contact_number"].includes(name)) {
      sanitizedValue = value.replace(/\D/g, "").slice(0, 10);
    }

    setFormData((prevState) => ({ ...prevState, [name]: sanitizedValue }));

    if (name === "student_email_address") {
      setEmailError(value && !validateEmail(value) ? "Please enter a valid email address" : "");
    }

    if (name === "student_whatsapp_number") {
      setWhatsappError(sanitizedValue && !validateNumber(sanitizedValue) ? "WhatsApp number must be exactly 10 digits" : "");
    }
    if (name === "student_alternative_number") {
      setAlternativeError(sanitizedValue && !validateNumber(sanitizedValue) ? "Alternative number must be exactly 10 digits" : "");
    }
    if (name === "student_emergency_contact_number") {
      setEmergencyError(sanitizedValue && !validateNumber(sanitizedValue) ? "Emergency contact number must be exactly 10 digits" : "");
    }

    if (name === "student_willow_relationship") {
      if (value === "No") {
        setFormData((prevState) => ({
          ...prevState,
          relation_type: "",
          relation_hr_contact: "",
          relation_branch: "",
          relation_name: "",
          relation_surname: "",
          relation_employee_code: "",
          relation_reference: "",
        }));
      }
    }
    if (name === "relation_type" && formData.student_willow_relationship === "Yes") {
      setFormData((prevState) => ({
        ...prevState,
        relation_hr_contact: "",
        relation_branch: "",
        relation_name: "",
        relation_surname: "",
        relation_employee_code: "",
        relation_reference: "",
      }));
    }
  };

  const handleFileChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      const fileNameWithoutExtension = file.name;
      setFormData((prev) => ({
        ...prev,
        employment_status_attachment_name: fileNameWithoutExtension,
        employment_status_attachment: file,
      }));
    }
  };

  const handleViewFile = () => {
    if (studentId) {
      window.open(`${API_BASE_URL}/student-details/view-attachment/${studentId}`, "_blank");
    }
  };

  const handleDeleteAttachmentClick = () => {
    setDeleteAttachmentConfirmationOpen(true);
  };

  const handleDeleteAttachmentConfirm = async () => {
    if (!studentId) return;
    setIsLoading(true);
    try {
      const response = await fetch(`${API_BASE_URL}/student-details/delete-attachment/${studentId}`, {
        method: "PUT",
      });
      if (response.ok) {
        setFormData((prev) => ({
          ...prev,
          employment_status_attachment: null,
          employment_status_attachment_name: "",
        }));
        // // setsuccessmessage("Attachment deleted successfully!");
        setDeleteAttachmentConfirmationOpen(false);
      } else {
        setErrorMessage("Failed to delete attachment. Please try again.");
      }
    } catch (err) {
      console.error("Error deleting attachment:", err);
      setErrorMessage("Error deleting attachment. Please try again.");
    } finally {
      setIsLoading(false);
    }
  };

  const handleDeleteAttachmentCancel = () => {
    setDeleteAttachmentConfirmationOpen(false);
  };

  const handleEmergencyContactChange = (e, newValue) => {
    setEmergencyContactOption(newValue);
    if (newValue === "Same as above") {
      setFormData((prevState) => ({
        ...prevState,
        student_emergency_contact_name: formData.student_name,
        student_emergency_contact_number: formData.student_whatsapp_number,
        student_emergency_contact_relationship: "Self",
        student_emergency_contact_address: formData.student_home_address,
      }));
      setEmergencyError(formData.student_whatsapp_number && !validateNumber(formData.student_whatsapp_number) ? "Emergency contact number must be exactly 10 digits" : "");
    } else {
      setFormData((prevState) => ({
        ...prevState,
        student_emergency_contact_name: "",
        student_emergency_contact_number: "",
        student_emergency_contact_relationship: "",
        student_emergency_contact_address: "",
      }));
      setEmergencyError("");
    }
  };

  const handleSave = async () => {
  // Validation checks
  if (formData.student_email_address && !validateEmail(formData.student_email_address)) {
    setEmailError("Please enter a valid email address");
    return;
  }
  if (formData.student_whatsapp_number && !validateNumber(formData.student_whatsapp_number)) {
    setWhatsappError("WhatsApp number must be exactly 10 digits");
    return;
  }
  if (formData.student_alternative_number && !validateNumber(formData.student_alternative_number)) {
    setAlternativeError("Alternative number must be exactly 10 digits");
    return;
  }
  if (formData.student_emergency_contact_number && !validateNumber(formData.student_emergency_contact_number)) {
    setEmergencyError("Emergency contact number must be exactly 10 digits");
    return;
  }

  const userId = user?.user_id;
  const url = studentId
    ? `${API_BASE_URL}/student-details/update/${studentId}`
    : `${API_BASE_URL}/student-details/insert`;
  const method = studentId ? "PUT" : "POST";

  const body = new FormData();
  Object.keys(formData).forEach((key) => {
    if (key !== "employment_status_attachment" && formData[key] !== null && formData[key] !== undefined) {
      body.append(key, formData[key]);
    }
  });
  if (formData.employment_status_attachment) {
    body.append("employment_status_attachment", formData.employment_status_attachment);
  }
  if (!studentId) {
    body.append("user_id", userId);
  }

  try {
    const response = await fetch(url, {
      method,
      body,
    });

    if (response.ok) {
      const savedStudent = await response.json();
      // Ensure savedStudent is a clean object
      const cleanStudent = {
        ...savedStudent,
        // Remove any problematic fields (e.g., binary data or non-serializable objects)
        employment_status_attachment: null,
        employment_status_attachment_name: savedStudent.employment_status_attachment_name || "",
      };
      // // setsuccessmessage(studentId ? "Updated successfully!" : "Created successfully!");
      onSave(cleanStudent); // Pass cleaned student data
      onClose(); // Close the drawer
    } else {
      console.error("Failed to save data:", response.statusText);
      // setsuccessmessage("Failed to save data. Please try again.");
    }
  } catch (error) {
    console.error("Error saving student data:", error);
    // setsuccessmessage("An error occurred. Please try again.");
    onClose(); // Close the drawer even on error
  }
};

  const handleDeleteClick = () => {
    setDeleteConfirmationOpen(true);
  };

  const handleDeleteConfirm = async () => {
    if (!studentId) return;
    setIsLoading(true);
    try {
      const response = await fetch(
        `${API_BASE_URL}/student-details/delete/${studentId}`,
        { method: "DELETE" }
      );

      if (!response.ok) {
        throw new Error(`Failed to delete student: ${response.statusText}`);
      }

      onDelete(studentId);
      onClose();
      setDeleteConfirmationOpen(false);
    } catch (error) {
      console.error("Error deleting student:", error);
      setErrorMessage("Failed to delete student. Please try again.");
    } finally {
      setIsLoading(false);
    }
  };

  const handleDeleteCancel = () => {
    setDeleteConfirmationOpen(false);
  };

  const filteredFinanceType = formData.student_religion === "Islam" 
    ? financeType 
    : financeType.filter(type => type !== "Zakah");

  const renderField = (key, index) => {
    const fieldStyles = {
      backgroundColor: isDarkMode ? '#1A202C' : '#ffffff',
      color: isDarkMode ? '#F7FAFC' : '#1E293B',
      borderRadius: '8px',
      '& .MuiInputBase-input': {
        color: isDarkMode ? '#F7FAFC' : '#1E293B',
      },
      '& .MuiFormLabel-root': {
        color: isDarkMode ? '#F7FAFC' : '#1E293B',
      },
    };
    const inputLabelProps = { style: { color: isDarkMode ? '#F7FAFC' : '#1E293B' } };

    const relationFields = [
      "relation_type",
      "relation_hr_contact",
      "relation_branch",
      "relation_name",
      "relation_surname",
      "relation_employee_code",
      "relation_reference",
    ];

    if (relationFields.includes(key)) {
      if (formData.student_willow_relationship !== "Yes") {
        return null;
      }
      if (key === "relation_type") {
        return (
          <Grid item xs={12} key={index}>
            <Autocomplete
              value={formData[key] || ""}
              onChange={(e, newValue) => handleChange({ target: { name: key, value: newValue } })}
              options={relationshipTypes}
              renderInput={(params) => (
                <TextField {...params} label="Relationship Type" sx={fieldStyles} InputLabelProps={inputLabelProps} />
              )}
            />
          </Grid>
        );
      }
      if (!formData.relation_type) {
        return null;
      }
      if (formData.relation_type === "Staff" || formData.relation_type === "Dependent of Staff") {
        if (["relation_hr_contact", "relation_branch", "relation_name", "relation_surname", "relation_employee_code"].includes(key)) {
          const labels = {
            relation_hr_contact: "HR Contact",
            relation_branch: "Branch",
            relation_name: "Name",
            relation_surname: "Surname",
            relation_employee_code: "Employee Code",
          };
          return (
            <Grid item xs={12} key={index}>
              <TextField
                label={labels[key]}
                name={key}
                fullWidth
                value={formData[key] || ""}
                onChange={handleChange}
                sx={fieldStyles}
                InputLabelProps={inputLabelProps}
              />
            </Grid>
          );
        }
        return null;
      }
      if (formData.relation_type === "Family") {
        if (key === "relation_name") {
          return (
            <Grid item xs={12} key={index}>
              <TextField
                label="Who are you related to"
                name={key}
                fullWidth
                value={formData[key] || ""}
                onChange={handleChange}
                sx={fieldStyles}
                InputLabelProps={inputLabelProps}
              />
            </Grid>
          );
        }
        if (key === "relation_reference") {
          return (
            <Grid item xs={12} key={index}>
              <TextField
                label="How are you related"
                name={key}
                fullWidth
                value={formData[key] || ""}
                onChange={handleChange}
                sx={fieldStyles}
                InputLabelProps={inputLabelProps}
              />
            </Grid>
          );
        }
        return null;
      }
      if (formData.relation_type === "Director/Board Member or stakeholder") {
        if (key === "relation_name") {
          return (
            <Grid item xs={12} key={index}>
              <TextField
                label="Please provide the person's name"
                name={key}
                fullWidth
                value={formData[key] || ""}
                onChange={handleChange}
                sx={fieldStyles}
                InputLabelProps={inputLabelProps}
              />
            </Grid>
          );
        }
        return null;
      }
      if (formData.relation_type === "Referral") {
        if (key === "relation_reference") {
          return (
            <Grid item xs={12} key={index}>
              <TextField
                label="Reference Relation"
                name={key}
                fullWidth
                value={formData[key] || ""}
                onChange={handleChange}
                sx={fieldStyles}
                InputLabelProps={inputLabelProps}
              />
            </Grid>
          );
        }
        if (key === "relation_name") {
          return (
            <Grid item xs={12} key={index}>
              <TextField
                label="Please provide the person's name"
                name={key}
                fullWidth
                value={formData[key] || ""}
                onChange={handleChange}
                sx={fieldStyles}
                InputLabelProps={inputLabelProps}
              />
            </Grid>
          );
        }
        return null;
      }
    }

    if (key === "student_date_of_birth") {
      return (
        <Grid item xs={12} key={index}>
          <LocalizationProvider dateAdapter={AdapterDateFns}>
            <DatePicker
              wrapperClassName={"datepicker"}
              className={"datepicker"}
              label="Student Date of Birth"
              name="student_date_of_birth"
              value={selectedDate}
              onChange={handleDateChange}
              format="MM/dd/yyyy"
              sx={fieldStyles}
              InputLabelProps={inputLabelProps}
            />
          </LocalizationProvider>
        </Grid>
      );
    }

    if (key === "student_number_of_siblings") {
      return (
        <Grid item xs={12} key={index}>
          <TextField
            label="Number of Siblings"
            name="student_number_of_siblings"
            type="number"
            fullWidth
            value={formData.student_number_of_siblings || ""}
            onChange={handleChange}
            sx={fieldStyles}
            InputLabelProps={inputLabelProps}
          />
        </Grid>
      );
    }

    if (key === "student_id_passport_number") {
      return (
        <Grid item xs={12} key={index}>
          <TextField
            label="Student ID/Passport Number"
            name="student_id_passport_number"
            type="text"
            fullWidth
            value={formData.student_id_passport_number || ""}
            onChange={handleChange}
            sx={fieldStyles}
            InputLabelProps={inputLabelProps}
          />
        </Grid>
      );
    }

    if (key === "student_email_address") {
      return (
        <Grid item xs={12} key={index}>
          <TextField
            label="Student Email Address"
            name="student_email_address"
            type="email"
            fullWidth
            value={formData.student_email_address || ""}
            onChange={handleChange}
            error={!!emailError}
            helperText={emailError}
            sx={fieldStyles}
            InputLabelProps={inputLabelProps}
          />
        </Grid>
      );
    }

    if (key === "student_whatsapp_number") {
      return (
        <Grid item xs={12} key={index}>
          <TextField
            label="Student WhatsApp Number"
            name="student_whatsapp_number"
            type="tel"
            fullWidth
            value={formData.student_whatsapp_number || ""}
            onChange={handleChange}
            error={!!whatsappError}
            helperText={whatsappError}
            inputProps={{ maxLength: 10, pattern: "[0-9]*" }}
            sx={fieldStyles}
            InputLabelProps={inputLabelProps}
          />
        </Grid>
      );
    }

    if (key === "student_alternative_number") {
      return (
        <Grid item xs={12} key={index}>
          <TextField
            label="Student Alternative Number"
            name="student_alternative_number"
            type="tel"
            fullWidth
            value={formData.student_alternative_number || ""}
            onChange={handleChange}
            error={!!alternativeError}
            helperText={alternativeError}
            inputProps={{ maxLength: 10, pattern: "[0-9]*" }}
            sx={fieldStyles}
            InputLabelProps={inputLabelProps}
          />
        </Grid>
      );
    }

    if (key === "student_emergency_contact_name") {
      return (
        <Grid item xs={12} key={index}>
          <Autocomplete
            value={emergencyContactOption}
            onChange={handleEmergencyContactChange}
            options={["Same as above", "Add new"]}
            renderInput={(params) => (
              <TextField {...params} label="Emergency Contact" sx={fieldStyles} InputLabelProps={inputLabelProps} />
            )}
          />
          {emergencyContactOption === "Add new" && (
            <TextField
              label="Student Emergency Contact Name"
              name="student_emergency_contact_name"
              fullWidth
              value={formData.student_emergency_contact_name || ""}
              onChange={handleChange}
              sx={{ ...fieldStyles, marginTop: 2 }}
              InputLabelProps={inputLabelProps}
            />
          )}
        </Grid>
      );
    }

    if (key === "student_emergency_contact_number" && emergencyContactOption === "Add new") {
      return (
        <Grid item xs={12} key={index}>
          <TextField
            label="Student Emergency Contact Number"
            name="student_emergency_contact_number"
            type="tel"
            fullWidth
            value={formData.student_emergency_contact_number || ""}
            onChange={handleChange}
            error={!!emergencyError}
            helperText={emergencyError}
            inputProps={{ maxLength: 10, pattern: "[0-9]*" }}
            sx={fieldStyles}
            InputLabelProps={inputLabelProps}
          />
        </Grid>
      );
    }

    if (key === "student_emergency_contact_relationship" && emergencyContactOption === "Add new") {
      return (
        <Grid item xs={12} key={index}>
          <TextField
            label="Student Emergency Contact Relationship"
            name="student_emergency_contact_relationship"
            fullWidth
            value={formData.student_emergency_contact_relationship || ""}
            onChange={handleChange}
            sx={fieldStyles}
            InputLabelProps={inputLabelProps}
          />
        </Grid>
      );
    }

    if (key === "student_emergency_contact_address" && emergencyContactOption === "Add new") {
      return (
        <Grid item xs={12} key={index}>
          <TextField
            label="Student Emergency Contact Address"
            name="student_emergency_contact_address"
            fullWidth
            value={formData.student_emergency_contact_address || ""}
            onChange={handleChange}
            sx={fieldStyles}
            InputLabelProps={inputLabelProps}
          />
        </Grid>
      );
    }

    if (key === "student_status") {
      return (
        <Grid item xs={12} key={index}>
          <Autocomplete
            value={formData[key] || "Pending"}
            onChange={(e, newValue) => handleChange({ target: { name: key, value: newValue } })}
            options={statusOptions}
            renderInput={(params) => (
              <TextField {...params} label="Student Status" sx={fieldStyles} InputLabelProps={inputLabelProps} />
            )}
            disabled={!isAdmin}
          />
        </Grid>
      );
    }

    if (key === "student_status_comment") {
      return (
        <Grid item xs={12} key={index}>
          <TextField
            label="Status Comment"
            name="student_status_comment"
            fullWidth
            multiline
            rows={4}
            value={formData.student_status_comment || ""}
            onChange={handleChange}
            sx={fieldStyles}
            InputLabelProps={inputLabelProps}
            disabled={!isAdmin}
          />
        </Grid>
      );
    }

    if (key === "student_willow_relationship") {
      return (
        <Grid item xs={12} key={index}>
          <Autocomplete
            value={formData[key] || ""}
            onChange={(e, newValue) => handleChange({ target: { name: key, value: newValue } })}
            options={yes_no}
            renderInput={(params) => (
              <TextField
                {...params}
                label="Does the student have any relationship to the Willowton Group?"
                sx={fieldStyles}
                InputLabelProps={inputLabelProps}
              />
            )}
          />
        </Grid>
      );
    }

    if (key === "student_nationality") {
      return (
        <Grid item xs={12} key={index}>
          <Autocomplete
            value={formData[key] || ""}
            onChange={(e, newValue) => handleChange({ target: { name: key, value: newValue } })}
            options={nationality}
            renderInput={(params) => (
              <TextField {...params} label="Student Nationality" sx={fieldStyles} InputLabelProps={inputLabelProps} />
            )}
          />
        </Grid>
      );
    }

    if (key === "student_province") {
      return (
        <Grid item xs={12} key={index}>
          <Autocomplete
            value={formData[key] || ""}
            onChange={(e, newValue) => handleChange({ target: { name: key, value: newValue } })}
            options={provinces}
            renderInput={(params) => (
              <TextField {...params} label="Student Province" sx={fieldStyles} InputLabelProps={inputLabelProps} />
            )}
          />
        </Grid>
      );
    }

    if (key === "student_type") {
      return (
        <Grid item xs={12} key={index}>
          <Autocomplete
            value={formData[key] || ""}
            onChange={(e, newValue) => handleChange({ target: { name: key, value: newValue } })}
            options={studentType}
            renderInput={(params) => (
              <TextField {...params} label="Student Type" sx={fieldStyles} InputLabelProps={inputLabelProps} />
            )}
          />
        </Grid>
      );
    }

    if (key === "student_religion") {
      return (
        <Grid item xs={12} key={index}>
          <Autocomplete
            value={formData[key] || ""}
            onChange={(e, newValue) => handleChange({ target: { name: key, value: newValue } })}
            options={religions}
            renderInput={(params) => (
              <TextField {...params} label="Student Religion" sx={fieldStyles} InputLabelProps={inputLabelProps} />
            )}
          />
        </Grid>
      );
    }

    if (key === "student_finance_type") {
      return (
        <Grid item xs={12} key={index}>
          <Autocomplete
            value={formData[key] || ""}
            onChange={(e, newValue) => handleChange({ target: { name: key, value: newValue } })}
            options={filteredFinanceType}
            renderInput={(params) => (
              <TextField {...params} label="Student Finance Type" sx={fieldStyles} InputLabelProps={inputLabelProps} />
            )}
          />
        </Grid>
      );
    }

    if (key === "student_race") {
      return (
        <Grid item xs={12} key={index}>
          <Autocomplete
            value={formData[key] || ""}
            onChange={(e, newValue) => handleChange({ target: { name: key, value: newValue } })}
            options={races}
            renderInput={(params) => (
              <TextField {...params} label="Student Race" sx={fieldStyles} InputLabelProps={inputLabelProps} />
            )}
          />
        </Grid>
      );
    }

    if (key === "student_marital_status") {
      return (
        <Grid item xs={12} key={index}>
          <Autocomplete
            value={formData[key] || ""}
            onChange={(e, newValue) => handleChange({ target: { name: key, value: newValue } })}
            options={maritalStatus}
            renderInput={(params) => (
              <TextField {...params} label="Student Marital Status" sx={fieldStyles} InputLabelProps={inputLabelProps} />
            )}
          />
        </Grid>
      );
    }

    if (key === "student_employment_status") {
      return (
        <Grid item xs={12} key={index}>
          <Autocomplete
            value={formData[key] || ""}
            onChange={(e, newValue) => handleChange({ target: { name: key, value: newValue } })}
            options={employmentStatus}
            renderInput={(params) => (
              <TextField {...params} label="Student Employment Status" sx={fieldStyles} InputLabelProps={inputLabelProps} />
            )}
          />
          <TextField
            label="Employment Status Attachment Name"
            name="employment_status_attachment_name"
            fullWidth
            disabled
            value={formData.employment_status_attachment_name || ""}
            sx={{
              ...fieldStyles,
              marginTop: 2,
              '& .MuiInputBase-input.Mui-disabled': {
                WebkitTextFillColor: isDarkMode ? '#F7FAFC' : '#1E293B',
              },
            }}
            InputLabelProps={inputLabelProps}
          />
          <Button
            variant="outlined"
            component="label"
            sx={{ ...fieldStyles, width: "100%", marginTop: 1, borderColor: isDarkMode ? '#F7FAFC' : '#1E293B' }}
          >
            Upload Employment Status Attachment
            <input type="file" hidden onChange={handleFileChange} />
          </Button>
          {formData.employment_status_attachment_name && studentId && (
            <>
              <Button
                variant="text"
                color="primary"
                onClick={handleViewFile}
                startIcon={<VisibilityIcon />}
                sx={{ width: "100%", marginTop: 1, color: isDarkMode ? '#F7FAFC' : '#1E293B' }}
              >
                View/Download Attachment
              </Button>
              <Button
                variant="outlined"
                color="error"
                onClick={handleDeleteAttachmentClick}
                startIcon={<DeleteIcon />}
                sx={{ width: "100%", marginTop: 1, borderColor: isDarkMode ? '#F7FAFC' : '#1E293B', color: 'red' }}
              >
                Delete Attachment
              </Button>
            </>
          )}
        </Grid>
      );
    }

    if (key === "student_highest_education") {
      return (
        <Grid item xs={12} key={index}>
          <Autocomplete
            value={formData[key] || ""}
            onChange={(e, newValue) => handleChange({ target: { name: key, value: newValue } })}
            options={highestEducation}
            renderInput={(params) => (
              <TextField {...params} label="Student Highest Education" sx={fieldStyles} InputLabelProps={inputLabelProps} />
            )}
          />
        </Grid>
      );
    }

    if (key === "student_date_stamp" || key === "id" || key === "user_id" || key === "student_industry" || key === "employment_status_attachment" || key === "employment_status_attachment_name") {
      return null;
    }

    let label = key.replace(/_/g, " ").toLowerCase().split(" ").map(word => word.charAt(0).toUpperCase() + word.slice(1)).join(" ");
    return (
      <Grid item xs={12} key={index}>
        <TextField
          label={label}
          name={key}
          fullWidth
          value={formData[key] || ""}
          onChange={handleChange}
          sx={fieldStyles}
          InputLabelProps={inputLabelProps}
        />
      </Grid>
    );
  };

  return (
    <Drawer anchor="right" open={open} onClose={onClose}>
      <Box sx={{ width: drawerWidth, height: "100%", display: "flex", flexDirection: "column", backgroundColor: isDarkMode ? '#2D3748' : '#fff' }}>
        <Box sx={{ padding: 2, borderBottom: "1px solid #ccc", backgroundColor: isDarkMode ? '#1E293B' : '#e1f5fe' }}>
          <Typography variant="h6" sx={{ fontWeight: "bold", color: isDarkMode ? '#F7FAFC' : '#1E293B' }}>
            Student Details
          </Typography>
          {/* {successMessage && (
            <Typography variant="body2" color="success.main" sx={{ mt: 1 }}>
              {successMessage}
            </Typography>
          )} */}
          {errorMessage && (
            <Typography variant="body2" color="error.main" sx={{ mt: 1 }}>
              {errorMessage}
            </Typography>
          )}
        </Box>

        <Box sx={{ flex: 1, overflowY: "auto", padding: 2 }}>
          {isLoading ? (
            <Box sx={{ display: "flex", justifyContent: "center", alignItems: "center", height: "100%" }}>
              <CircularProgress />
            </Box>
          ) : (
            <Grid container spacing={2}>
              {Object.keys(formData).map((key, index) => renderField(key, index))}
            </Grid>
          )}
        </Box>

        <Divider />
        <Box sx={{ padding: 2, borderTop: "1px solid #ccc", display: "flex", justifyContent: "space-between" }}>
          <Box sx={{ display: "flex", gap: 1 }}>
            <Button
              onClick={onClose}
              variant="outlined"
              size="small"
              sx={{ borderColor: isDarkMode ? '#F7FAFC' : '#1E293B', color: isDarkMode ? '#F7FAFC' : '#1E293B' }}
              startIcon={<CloseIcon />}
              disabled={isLoading}
            >
              Close
            </Button>

            {studentId && (
              <Button
                onClick={handleDeleteClick}
                variant="outlined"
                size="small"
                sx={{ borderColor: isDarkMode ? '#F7FAFC' : '#1E293B', color: 'red' }}
                startIcon={<DeleteIcon />}
                disabled={isLoading}
              >
                Delete
              </Button>
            )}
          </Box>

          <Box sx={{ display: "flex", gap: 1 }}>
            {!studentId ? (
              <Button
                onClick={handleSave}
                variant="contained"
                size="small"
                color="primary"
                startIcon={<AddIcon />}
                disabled={isLoading}
              >
                {isLoading ? <CircularProgress size={20} /> : "Create"}
              </Button>
            ) : (
              <Button
                onClick={handleSave}
                variant="contained"
                size="small"
                color="primary"
                startIcon={<SaveIcon />}
                disabled={isLoading}
              >
                {isLoading ? <CircularProgress size={20} /> : "Save"}
              </Button>
            )}
          </Box>
        </Box>

        <Dialog
          open={deleteConfirmationOpen}
          onClose={handleDeleteCancel}
        >
          <DialogTitle>Confirm Deletion</DialogTitle>
          <DialogContent>
            Are you sure you want to delete this record?
          </DialogContent>
          <DialogActions>
            <Button onClick={handleDeleteCancel} color="primary" disabled={isLoading}>
              Cancel
            </Button>
            <Button onClick={handleDeleteConfirm} color="error" disabled={isLoading}>
              {isLoading ? <CircularProgress size={20} /> : "Delete"}
            </Button>
          </DialogActions>
        </Dialog>

        <Dialog
          open={deleteAttachmentConfirmationOpen}
          onClose={handleDeleteAttachmentCancel}
        >
          <DialogTitle>Confirm Attachment Deletion</DialogTitle>
          <DialogContent>
            Are you sure you want to delete the employment status attachment?
          </DialogContent>
          <DialogActions>
            <Button onClick={handleDeleteAttachmentCancel} color="primary" disabled={isLoading}>
              Cancel
            </Button>
            <Button onClick={handleDeleteAttachmentConfirm} color="error" disabled={isLoading}>
              {isLoading ? <CircularProgress size={20} /> : "Delete"}
            </Button>
          </DialogActions>
        </Dialog>
      </Box>
    </Drawer>
  );
};

export default StudentDetailDrawer;